const uploadFormData = async (req, res) => {
    let sampleFile
    let uploadPath
    // const { user } = req
    const {type, userId} = req.body
    console.log(type)

    if (!req.files || Object.keys(req.files).length === 0) {
        return res.send({response_status: 'ERROR', message: 'No files were uploaded.'})
    }

    sampleFile = req.files.files
    const name = `${
        makeid(5)
    }-${
        req.body.filename
    }`
    uploadPath = `public/upload-file/${name}`

    await sampleFile.mv(uploadPath, async (err) => {
        if (err) {
            return res.send({
                response_status: 'ERROR',
                message: err.message || 'Some error occurred while retrieving the files.'
            })
        }
        const file = await File.create({type, name, path: `https://gscm-service.yuzudigital.com/${uploadPath}`})
        res.send({response_status: 'SUCCESS', data: file.dataValues, message: 'File was uploaded successfully'})

    })
}

function makeid(length) {
    var result = "";
    var characters = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    var charactersLength = characters.length;
    for (var i = 0; i < length; i++) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
}

